import React from 'react'
import hinhceh from '../access/img/CEH.jpg'
import { NavLink } from 'react-router-dom'

function ContentCPCEH() {
  return (
    <div className="container" id="about">
  <h3> CHINH PHC NGAY CHNG CH CEH VI iSPACE </h3>
  <hr />
  <div className="row" style={{ marginTop: 50 }}>
    <div className="col-md-5 py-3 py-md-0">
      <div className="card">
        <img src={hinhceh} alt="" />
      </div>
    </div>
    <div
      className="col-md-7 py-3 py-md-0"
      style={{ color: "rgb(233, 141, 11)" }}
    >
      <p className='mt-12 text-dark fz-18 line-h-16'>
         Khm ph ngay kha hc CEH ti iSPACE, kha hc dnh cho ngi mun
        chinh phc chng ch CEH ch sau 40 gi hc.
      </p>
      <li className='text-dark fz-18 line-h-16'>
        Bn l chuyn vin cng ngh thng tin mun nng cao kin thc v an
        ninh mng?
      </li>
      <li className='text-dark fz-18 line-h-16 mt-12'>
        Nhng c qu nhiu kin thc v l hng, bo mt khin bn khng bit
        nn hc nh th no?
      </li>
      <li className='mb-15 text-dark fz-18 line-h-16 mt-12'>
        Bn mong mun c mt trung tm gip bn hc nhanh, nm chc kin thc 
        chinh phc ngay chng ch CEH?
      </li>
      <NavLink className='btn btn-dangky text-white' to='/tuyenSinhiSpace/khoa-hoc-ceh'>Xem chi tit</NavLink>
    </div>
  </div>
</div>

  )
}

export default ContentCPCEH